<template>
<div>
 <div class="title">
 	热销推荐
 </div>
 <ul>
   
 	<router-link tag="li" class="item" v-for="item in   List"  
 	:to="'/detail/'+item.id">
 	  <div class="item-img">
 	  	<img :src="item.imgUrl" alt="">
 	  </div>
 	  <div class="item-text">
 		<p class="item-title" v-text="item.title"></p>
 		<p class="item-desc" v-text="item.desc"></p>
 		<button class="item-button">查看详情</button>
 	  </div>
 	</router-link>

 </ul>
</div>
</template>
<script type="text/javascript">
   export default{
    props:{
    List:Array
    },
   	  data(){
  	return{
  		
         	} 
           }
 }
</script>
<style type="text/css" media="screen" lang="stylus" scoped>
	.title{
       line-height: .8rem;
       background-color: #eee;
       margin-top: .2rem;
	}
	.item{
		height: 1.9rem;
		overflow: hidden;
	}
	.item-img{
		float: left;
		margin: .1rem;
		margin-right: .2rem;
	}
	.item-img img{
		width: 1.7rem;
		height: 1.7rem;
	}
	.item-text{
		float: left;
	}
	.item-title{
		width: 2rem;
		margin-top: .1rem;
		line-height: .54rem;
        font-size: .32rem;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
	}
	.item-desc{
		width: 4rem;
		line-height: .4rem;
        color: #ccc;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
	}
	 .item-button{
        line-height: .44rem;
        margin-top: .20rem;
        background: #ff9300;
        padding: 0 .2rem;
        border-radius: .06rem;
        color: #fff;
    }
</style>


