<template>
	<div class="icons">
	 <swiper :options="swiperOption" >
	  <swiper-slide v-for="(page,index) in pages">
		<div class="icon" v-for="item in page "><img class="icon-img" :src="item.imgUrl"/><p class="icon-p">{{item.desc}}</p></div>
	  </swiper-slide>	
	 </swiper> 	
	</div>
</template>
<script type="text/javascript">
  import homeSwiper from "./swiper"

	export default{
		props:{
			iconsList:Array
		},
	  data(){
	  	return{
	  		swiperOption:{
        		 autoplay: false,             
	  	}
	  }
	  },
	  components:{
	  	homeSwiper,
	  },
	  computed:{
	  	pages(){
	  		var pages=[]
	  		this.iconsList.forEach(function(item,index){
              var page=Math.floor(index/8)
               if (!pages[page]) {
                    pages[page] = []
                      }
              pages[page].push(item)
              
	  		})
	  		return pages
	  	}
	  }
	}
</script>

<style type="text/css" media="screen" lang="stylus" scoped>
	.icons{
		width: 100%;
		height:50vw;
	}
	.icon{
		width: 25%;
		height: 25vw;
		padding-top: .1rem;
		float: left;
	}
	.icon-img{
		display: block;
		height: 60%;
		margin: 0 auto
	}
	.icon-p{
		text-align: center;
		margin-top: .1rem;
	}
</style>


