	<template>
		<div>
		   <div class="weekend-title">
		   	周末去哪儿
		   </div>
		   <ul>
				<li v-for="item in weekendList">
					<div class="weekend-item-img">
						<img :src="item.imgUrl" alt="">
					</div>
					 <div class="weekend-item-info">
                     <p class="weekend-item-title" v-text="item.title"></p>
                     <p class="weekend-item-desc" v-text="item.desc"></p>
                     </div>
				</li>
		   </ul>
		</div>
	</template>
<script type="text/javascript">
	export default{
		props:{
			weekendList:Array
		},
		data(){
			return{
				
			}
		}
	}	
</script>
<style type="text/css" media="screen">
	.weekend-title{
       line-height: .8rem;
       background-color: #eee;
       margin-top: .2rem;
	}
	.weekend-item-img{
		overflow: hidden;
		height: 0;
		padding-bottom: 33.9%;
	}
	.weekend-item-img img{
		width: 100%;
	}
	.weekend-item-title{
		margin:0.1rem 0;
	}
	.weekend-item-desc{
		margin-bottom: .1rem
	}
</style>


