import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
export default new Router({
  routes: [
    {
		path:"/",   
		redirect:"/homeRouter"	   
	},
    {
      path: '/city',
      name: 'City',
      component: () => import('../page/city')
    },
     {
      path: '/homeRouter',
      name: 'homeRouter',
      component: () => import("../components/homeRouter")
    },
     {
      path: '/detail/:id',
      name: 'Detail',
      component: () => import('../page/Detail')
    },
  ],
  scrollBehavior(to,from,savedPosition){
    return {x:0,y:0}
  }
})



