<template>
<div class="list"  ref="wrapper">
  <div>
	<div class="area">
		<div class="title border-topbottom">当前城市</div>
		<div class="button-list">
			<div class="button-wrapper"> <div class="button">{{this.currentCity}}</div></div>
		</div>
	</div>
	<div class="area">
		<div class="title border-topbottom">热门城市</div>
		<div class="button-list">
			<div class="button-wrapper" 
			     v-for="item in hot" 
			     @click="handleCityClick(item.name)"
			     > <div class="button">{{item.name}}</div>
			 </div>
		
		</div>
	</div>
	<div class="area" v-for="(item,key) in cities" :ref="key">
		<div class="title border-topbottom">{{key}}</div>
		<div class="item-list">
			<div class="item" 
			v-for="innerItme in item"
			@click="handleCityClick(innerItme.name)"
			>{{innerItme.name}}
			</div>
			
		</div>
	</div>
	
  </div>
</div>
</template>
<script type="text/javascript">
 import Bscroll from "better-scroll"
 import {mapState,mapMutations} from "vuex"
	export default{
	   props:{
	   	hot:Array,
	   	cities:Object,
	   	letter:String
	   },
	   computed:{
         ...mapState({
         	currentCity:"city"
         })
	   },
       mounted(){
       	 this.scroll=new Bscroll(this.$refs.wrapper, { mouseWheel: true, click: true, tap: true })
       },
       methods:{
           handleCityClick(city){
           	this.changeCity(city)
           	this.$router.push("/")
           },
           ...mapMutations(["changeCity"])
       },
       watch:{
       	letter(){
       		if(this.letter){
       		const element=this.$refs[this.letter][0]
       		this.scroll.scrollToElement(element)
       		                }
       	}
       }
	}
</script>
<style type="text/css" media="screen"  scoped>

    .list{
    	overflow: hidden;
    	position: absolute;
    	top: 1.64rem;
    	bottom: 0;
    	left: 0;
    	right: 0;
    }
    .border-topbottom{
    	border-top:#ccc solid .01rem;
    	border-bottom:#ccc solid .01rem;
    }
	.title{
		line-height: .44rem;
		background-color: #eee;
		padding-left: .2rem;
		color: #666;
		font-size: .26rem;
	}
	.button-list{
		overflow: hidden;
		padding: .1rem .6rem .1rem .1rem;

	}
	.button-wrapper{
        float: left;
        width: 33.33%;
	}
	.button{
		padding: .1rem 0;
		text-align: center;
		margin: .1rem;
		border: .02rem solid #ccc;
		border-radius: .06rem;
	}
	.item-list .item{
		line-height: .76rem;
		padding-left: .2rem;
		border-bottom: .01rem solid #ccc;
	}
</style>



