<template>
<div class="wrapper">
 <div v-if="cityActive">
	<home-header :city="city"  @citys="cityshow"></home-header>
	<home-swiper :swiperList="swiperList"></home-swiper>
	<home-icons  :iconsList="iconsList"></home-icons>        
	<home-recommend  :List="List"></home-recommend>
	<weekend :weekendList="weekendList"></weekend>
 </div>	
 <div v-if="!cityActive">
 	<city></city>
 </div>
</div> <!-- wrapper -->
</template>
<script type="text/javascript">
	import homeHeader from "./page/homeHeader"
	import homeSwiper from "./page/swiper"
	import homeIcons from "./page/icons"
	import homeRecommend from "./page/recommend"
	import weekend from "./page/weekend"
	import axios from "axios"
	import city from "./page/city"
	export default{
		components:{
			homeHeader,
			homeSwiper,
			homeIcons,
			homeRecommend,
			weekend,
			city
		},
		data(){
			return{
				 bb:"hjkhkj",
                 city:"",
                 swiperList:[],
                 iconsList:[],
                 List:[],
                 weekendList:[],
                 cityActive:true,
			}
		},
		methods:{
			getHomeInfo(){
				axios.get('/api/index.json').then(this.getHomeInfoSucc)
			},
			getHomeInfoSucc(res){
				res=res.data
				if(res.ret && res.data){
					this.city=res.data.city
					this.swiperList=res.data.swiperList
					this.iconsList=res.data.iconList
					this.List=res.data.recommendList
					this.weekendList=res.data.weekendList
					
				}
			},
			cityshow(){
				this.cityActive=!this.cityActive
			}
		},
		mounted (){
            this.getHomeInfo() 
		}
	}
</script>

<style type="text/css" media="screen">
	
</style>


