<template>
<div class="wrapper">
    <swiper :options="swiperOption"  v-if="swiperList.length">
    <!-- @someSwiperEvent="callback" -->
    <!-- slides -->
    <swiper-slide v-for="item in swiperList"><img :src="item.imgUrl" class="swiper-img"></swiper-slide>
    <!-- Optional controls -->
    <div class="swiper-pagination"  slot="pagination"></div>
  </swiper> 	
</div> 
</template>
<script type="text/javascript">
	export default{
        props:{
            swiperList:Array
        },
        data (){
        	return{
        		swiperOption:{
        			pagination:".swiper-pagination",
                    loop:true,
                    autoplay:3000,

        		}
        	}
        },
       
}
</script>

<style type="text/css" media="screen" scoped>
    .wrapper{
    	width: 100%;
    	height: 35vw;
    	overflow: hidden;
    }
	.swiper-img{
		width: 100%;
	}
	.wrapper >>> .swiper-pagination-bullet-active{
		background-color: #fff;
	}
</style> 


