<template>
	<div class="header">
	     <div  class="left"><span class="iconfont">&#xe624;</span></div>
	     <div  class="input"><span class="iconfont">&#xe632;</span> 城市/游玩/景点</div>
	     <router-link to="/city">
	       <div  class="right" style="color: #fff" >{{this.city}} <span class="arrow arrow-down"></span></div>
	     </router-link>
	</div>
</template>
<script type="text/javascript">
import { mapState,mapGetters} from "vuex"
export default{ 
	data(){
		return{
					num:0
		}

	},
	methods:{

	},
	computed:{
		...mapState(["city"]),
		...mapGetters(["doubleCity"])
	}
}
	
</script>

<style lang="stylus" scoped>
@import "../assets/varibles.styl"

* { touch-action: none; } 
    .header{
    	display: flex;
    	background-color: $bgColor;
    	height: .86rem
    }
    .iconfont{
    	color:#fff;
    }
	.left{
        float: left;
        width:.66rem;
        height: .86rem;
        line-height: .86rem;  
        margin-left: .2rem;
	}
	.input{
		flex: 1;
		height: .64rem;
		background-color: #fff;
		color: #ccc;
		border-radius: .1rem;
		margin-top:.12rem;
		margin-left: .2rem;
		line-height: .64rem;
		padding-left: .2rem;
	}
	.right{
		min-width: 1.04rem;
		padding:0 .1rem;
		float: right;
		text-align: center;
		line-height: .86rem;
		color: #fff,
	}
	.arrow {
        width: 0;
        height: 0;
        display: inline-block;
    }

    .arrow-down {
        border-left: .15rem solid transparent;
        border-right: .15rem solid transparent;
        border-top: .15rem solid #fff;
    }


</style>


